.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header a,
.App-header a:visited { /* This targets both unvisited and visited links */
  color: white; /* Set your desired color here */
  text-decoration: none; /* Removes the underline from links */
}

.App-header a:hover,
.App-header a:focus {
  text-decoration: underline; /* Adds an underline when hovering or focusing on the link */
  color: #61dafb; /* Optional: Changes color on hover or focus for better user experience */
}


.Button {
  padding: 10000px
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
